import {asyncComponent} from 'nc-lightapp-front';
import Listgrp from '../list';
const  Cardgrp = asyncComponent(()=>import(/* webpackChunkName: "uapbd/sminfo/payment_org/card/Cardgrp" *//*webpackMode:"eager"*/'../card'));
const routes = [
    {
        path:'/',
        component:Listgrp,
        exact:true
    },
    {
        path:'/list',
        component:Listgrp
    },
    {
        path:'/card',
        component:Cardgrp
    }
    ]
export default routes;